@import url(https://fonts.googleapis.com/css?family=Amatic+SC);
body {
  background-color: #FFFFFF;
  margin: 0;
  padding: 0;
  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  margin: 20px;
}

header {
  text-align: center;
  font-size: 3em;
  font-family: 'Amatic SC';
}

#editor {
  width: 100%;
  height: 60vh;
  font-size: 15px;
  border: none;
  margin-top: 1em;
  margin-bottom: -1em;
}

#preview {
  height: 61vh;
  overflow: auto;
}

#container {
  display: -webkit-flex;
  display: flex;
}

h2, textarea {
  color: #313D6F;
}

header {
  color: #FC9399;
}

.column {
  -webkit-flex: 50% 1;
          flex: 50% 1;
  margin: 10px;
}

.box {
  border: 2px solid #68D6E4;
  padding: 10px;
}

#preview-title {
  text-align: right;
  font-family: 'Amatic SC';
}

#editor-title {
  text-align:left;
  font-family: 'Amatic SC';
}

